@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap");

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  overflow: hidden;
}

/* Container principal com gradiente animado */
.home-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(-45deg, #000000, #3b006a, #000000, #5e1d9e);
  background-size: 400% 400%;
  animation: gradientFlow 12s ease infinite;
}

/* Efeito do gradiente se movendo lentamente */
@keyframes gradientFlow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Conteúdo */
.home-content {
  display: flex;
  align-items: center;
  gap: 3rem;
  max-width: 1000px;
  padding: 0 2rem;

}

.logo {
  font-size: 4.5rem;
  font-weight: 700;
  color: white;
  letter-spacing: 2px;
  text-transform: lowercase;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
}

.text {
  max-width: 480px;
  color: #e2e2e2;
  line-height: 1.7;
  font-size: 1rem;
  opacity: 0.95;
}